import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter as Router , Route} from "react-router-dom"
import {createStore, combineReducers, applyMiddleware }from "redux";
import {Provider} from "react-redux";
import saga from "redux-saga";

//style
import './index.css';

//Reducers

//Containers
import Login from './Containers/LogIn/index';
import SignUp from './Containers/SignUp';
import AgeVerification from './Containers/AgeVerification';


import * as serviceWorker from './serviceWorker';


const reducer = combineReducers({
    // auth: loginReducer,
    // counter: counterReducer
});

//Creating store and applying middleware
const createStoreWithMiddleware = applyMiddleware(saga)(createStore)
const store = createStoreWithMiddleware(reducer)

//Routing
const appRoute = (
    <Provider store = {store}> 
        <Router>
            <div>
                <Route exact path="/" component = {Login} />
                <Route exact path="/SignUp" component = {SignUp} />
                <Route exact path="/AgeVerification" component = {AgeVerification} />
            </div>
        </Router>
    </Provider>

)

ReactDOM.render(appRoute, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
