import React, {Component}from "react";

import './style.css';

class ToggleMenu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name : [],
        }
    }

    static getDerivedStateFromProps = (nextProps, prevProps) => {
        let name = []
        return {
            name : name.push(nextProps.name) 
        }
    }

    handleClick = (e) => {
        console.log(e);
        if (this.state.name === 'Go Back') {
            console.log('back')
        }
        else if(this.state.name === 'Sign Up') {
            console.log('sign up')
        }
    }

    render() {
        return(
            <div className = 'ToggleButton'>
                <button type="button" className={this.props.class} onClick={(e) => this.handleClick(e)}>
                    {this.props.type === 'text'? this.props.value : <img src={this.props.value} />}
                </button>
            </div>
        )
    }
}

export default ToggleMenu;